.page-header {
  width: 320px;
  margin: 0 auto;
}

.page-header__wrapper {
  width: 320px;
  margin: 0 auto;
  margin-bottom: 5px;
  padding: 15px;
  display: flex;
  box-sizing: border-box;
  justify-content: space-around;
  align-items: center;
  text-align: center;
}

.page-header__title-logo-image {
  display: none;
}

.main-nav__list {
  display: none;
}

@media (min-width: $tablet-width) {
  .page-header {
    width: 100%;
    min-height: 85px;
    margin: 0 auto;
  }

  .page-header__wrapper {
    width: 768px;
    margin: 0 auto;
    display: flex;
  }

  .page-header__logo-image,
  .page-header__title {
    display: none;
  }

  .page-header__title-logo-image {
    display: block;
  }

  .main-nav__list {
    width: 400px;
    display: flex;
    margin-left: auto;
    justify-content: space-around;
    // background-color: pink;
  }

  .main-nav__toggle {
    display: none;
  }

}

@media (min-width: $desktop-width) {
  .page-header {
    min-height: 130px;
  }

  .page-header__wrapper {
    width: 1200px;
    margin: 0 auto;
  }

  // .main-nav__list {
  //   width: 50%;
  //   margin-right: 0;
  //   padding-right: 0;
  // }

}
