.page-footer {
  width: 100%;
}

.map {
  display: block;
  text-align: center;
  background-image: url("../img/map.png");
  // background-position: 70% 50%;
}

.page-footer__wrapper {
  width: 320px;
  margin: 0 auto;
  background-color: $main-color;
}

.footer-logo {
  display: block;
  text-align: center;
  margin: 0 auto;
  padding-top: 40px;
  padding-bottom: 40px;
  background-color: $main-color;
  height: 22px;
}

.page-footer__social {
  width: 280px;
  margin: 0 20px;
  border-top: 0.5px solid #dcdcdc;
  border-bottom: 0.5px solid #dcdcdc;
  height: 70px;
}

.page-footer__copyright {
  height: 85px;
}



@media (min-width: $tablet-width) {

  .page-footer {
  width: 100%;
  height: 150px;
  background-color: $main-color;
  }

  .page-footer__wrapper {
  width: 768px;
  height: 150px;
  margin: 0 auto;
  display: flex;
  padding-top: 45px;
  box-sizing: border-box;
  }

  .footer-logo {
    padding-top: 20px;
  }

  .page-footer__social  {
    border: none;
  }

}


@media (min-width: $desktop-width) {

  .map  {
  margin-top: -110px;
  }


}
