.slogan {
  width: 320px;
  margin: 0 auto;
  }

.slogan__wrapper {
  width: 100%;
  padding: 0;
  min-height: 265px;
  margin: 0 auto;
  background-size: 320px;
  background-color: $bright-color;
  background-repeat: no-repeat;
  background-image: url("../img/green-cat.png");
  padding-top: 10px;
  text-align: center;
}


.slogan__image {
  display: block;
  margin: auto;
  margin-top: -110px;
  position: relative;
}

.slogan__link {
  position: absolute;
  top: 520px;
  margin-left: 20px;
}

@media (min-width: $tablet-width) {
  .slogan {
  width: 100%;
  }
  .slogan__wrapper {
  width: 768px;
  margin: 0 auto;
  background-color: inherit;
  background-image: none;
  text-align: left;
  box-sizing: border-box;
  padding-left: 50px;
  }

  .slogan__image {
    width: 60%;
    height: 60%;
  }

  .slogan__link {
    top: 200px;
    margin-left: 0;
  }

}

@media (min-width: $desktop-width) {

  .slogan__wrapper {
    width: 1200px;
  }

  .slogan__link {
    top: 300px;
  }
    .slogan__image {
      margin-top: -300px;
      margin-left: 300px;
    }

}
