.options {
  width: 320px;
  margin: 0 auto;
}

.options__wrapper{
  margin: 0 auto;
}



.options__list {
  margin: 0 auto;
  padding: 0;
  margin: 40px auto;
}

.options__item {
  list-style: none;
  box-sizing: border-box;
  display: block;
  min-height: 265px;
  width: 280px;
  background-color: $main-color;
  margin: 15px auto;
  padding: 10px 20px;
}

@media (min-width: $tablet-width) {
  .options {
  width: 100%;
  }
  .options__wrapper {
  width: 768px;
  margin: 0 auto;
  }

  .options__item {
  width: 708px;
  }

  //
  // .options__link {
  // margin-left: 50px;
  // margin-top: -500px;
  // margin-bottom: 440px;
  // }
}

@media (min-width: $desktop-width) {

  .options__wrapper {
    width: 1200px;
    padding: 0;
  }

  .options__list {
    display: flex;
  }

  .options__item {
    width: 570px;
    margin: 20px;
  }


  }
