.example {
  width: 320px;
  margin: 0 auto;

}

.example__wrapper {
  box-sizing: border-box;
  padding: 15px;
  background-color: $main-color;
}

@media (min-width: $tablet-width) {
  .example {
  width: 100%;

  }

  .example__wrapper {
    width: 768px;
    border-top: 0.5px solid #dcdcdc;
    margin: 0 auto;
    background-color: white;
  }

  .example__extra-wrapper {
    margin-top: -170px;
    padding-top: 180px;
    background-color: $main-color;
  }

}

@media (min-width: $desktop-width) {

  .example__wrapper {
  width: 1200px;
  }


}
