.copyright {
  padding: 30px;
  display: flex;
  box-sizing: border-box;
  justify-content: space-between;
}

.copyright__link {
  text-decoration: none;
  color: #444;
  padding-right: 15px;
}

@media (min-width: $tablet-width) {
  .copyright {
    justify-content: flex-end;
    padding-top: 20px;
  }


}
