.for-dilers {
  width: 320px;
  margin: 0 auto;
  padding: 10px;
  box-sizing: border-box;
  display: flex;
  min-height: 115px;
  justify-content: space-around;
  align-items: center;
}

.for-dilers__grey {
  background-color: $main-color;
}

@media (min-width: $tablet-width) {
  .for-dilers {
  width: 768px;
  display: flex;
  justify-content: space-around;
  }

  .for-dilers__p:first-child {
    width: 140px;
    text-align: left;
    text-transform: uppercase;
  }

  .for-dilers__p:last-child {
    width: 180px;
    text-align: right;
  }


}

@media (min-width: $desktop-width) {

  .for-dilers  {
  width: 570px;
  }


}
